.orders-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 1rem 7rem;
  height: 100vh;
}
.orders-list_title {
}

.orders-list_filters {
  padding: 1rem;
  border: 1px solid var(--accent-color-2);
  border-radius: 1rem;
  background-color: var(--glas);
}
.orders-list_filter-group {
  display: flex;
  gap: 1rem;
}

.orders-list_orders-container {
  overflow-y: auto;
  height: 100%;
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
}
.orders-list_order-box {
  border: 1px solid var(--accent-color-2);
  border-radius: 1rem;
  padding: 1rem;
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
  background-blend-mode: overlay;
  background-size: cover;
  background-position: center;
}
.orders-list_order-top {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}
.orders-list_order-name {
  color: var(--accent-color);
  font-weight: 600;
  font-size: 1.8rem;
  text-align: right;
}
.orders-list_order-cafe {
  font-weight: 600;
  /* font-size: 1.5rem; */
}
.orders-list_order-status {
  align-self: flex-start;
  font-weight: 500;
}
.orders-list_order-bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.orders-list_order-date {
  margin: 0;
  background-color: rgba(255, 255, 255, 0.4);
  padding: 0.5rem;
  border-radius: 1.5rem;
  display: inline-flex;
  border: 1px solid white;
  height: 3rem;
  align-items: center;
}
